*,
*::before,
*::after {
    box-sizing: border-box;
}

*:focus{
    outline: 1px dashed #ffffff;
    outline-offset: 0;
}

html {
    font: 62.5% / 1.15 sans-serif;
}

h1, h2 {
    margin-bottom: 0;
    color: rgb(255, 255, 255);
}

ul {
    list-style: none;
    padding: 0;
    color: rgb(255, 255, 255);
}

button {
    border: none;
    margin: 5px;
    padding: 0;
    width: auto;
    overflow: visible;
    background: transparent;
    color: rgb(255, 255, 255);
    line-height: normal;
}

button::-moz-focus-inner {
    border: 0;
}

button, input, optgroup, select, textarea {
    font-family: inherit;
    font-size: 100%;
    line-height: 1.15;
    margin: 0;
    background-color: rgb(54, 95, 153);
}

button, input {
    overflow: visible;
}

input[type="text"] {
    border-radius: 0;
    background-color: rgb(236, 231, 231);;
}

body {
    width: 100%;
    max-width: 68rem;
    margin: 0 auto;
    font: 1.6rem/1.25 Arial, sans-serif;
    background-color: #4d3d3d;
    color: #4d4d4d
}

.form-group > input[type="text"] {
    display: inline-block;
    margin-top: 0.4rem;
}

.btn {
    padding: 0.8rem 1rem 0.7rem;
    border: 0.2rem solid #4d4d4d;
    cursor: pointer;
    text-transform: capitalize;
}

.btn.toggle-btn {
    border-width: 1px;
    border-color: #000000
}

.btn.toggle-btn[aria-pressed="true"] {
    text-decoration: underline;
    border-color: #000000;
    border-width: 2px solid;
}

.btn__danger {
  color: #fff;
  background-color: #bd2130;  
}

.btn__filter {
    border-color: rgb(119, 4, 4);
}

.btn__primary {
    color: #fff;
    background-color: rgb(14, 243, 7);
}

.btn-group {
    display: flex;
    justify-content: space-between;
}

.btn-group > * {
    flex: 1 1 49%;
}

.btn-group > * + * {
    margin-left: o.8rem;
}

.label-wrapper {
    margin: 2px;
    flex: 0 0 100%;
    text-align: center;
}

.visually-hidden {
    position: absolute;
    height: 1px;
    width: 1px;
    overflow: hidden;
    clip: rect(1px 1px 1px 1px);
    clip: rect(1px, 1px, 1px, 1px);
    white-space: nowrap;
}

[class*="stack"] > * {
    margin-top: 0;
    margin-bottom: 0;
}

.stack-small > * + * {
    margin-top: 1.25rem;
  }
  .stack-large > * + * {
    margin-top: 2.5rem;
  }

  .stack-exception {
      margin-top: 1.2rem;
  }

  .exerciseapp {
      background: rgb(19, 18, 18);
      margin: 2rem 0 4rem 0;
      padding: 1rem;
      position: relative;
      box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 2.5rem 5rem 0 rgba(0, 0, 0, 0.1);
  }

  .exerciseapp > * {
      max-width: 50rem;
      margin-left: auto;
      margin-right: auto;
  }

  .exerciseapp > form {
      max-width: 100%;
  }

  .exerciseapp > h1 {
      display: block;
      max-width: 100%;
      text-align: center;
      margin: 0;
      margin-bottom: 1rem;
  }

  .label__lg {
      line-height: 1;
      font-weight: 300;
      padding: 0.8rem;
      margin-bottom: 1rem;
      text-align: center;
  }

  .input__lg {
      padding: 2rem;
      border: 2px solid #000;
  }

  .input__lg:focus {
    border-color: #4d4d4d;
    box-shadow: inset 0 0 0 2px;
  }

  [class*="__lg"] {
    display: inline-block;
    width: 100%;
    font-size: 1.9rem;
  }

  [class*="__lg"]:not(:last-child) {
    margin-bottom: 1rem;
  }

  .filters {
      width: 100%;
      margin: unset auto;
  }

  .exercise {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
  }

  .exercise > * {
      flex: 0 0 100%;
  }

  .exercise-text {
    width: 100%;
    min-height: 4.4rem;
    padding: 0.4rem 0.8rem;
    border: 2px solid #565656;
  }

  .exercise-text:focus {
    box-shadow: inset 0 0 0 2px;
  }

  .c-cb {
    box-sizing: border-box;
    font-family: Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 1.25;
    display: block;
    position: relative;
    min-height: 44px;
    padding-left: 40px;
    clear: left;
  }
  
  .c-cb > label::before,
  .c-cb > input[type="checkbox"] {
    box-sizing: border-box;
    top: -2px;
    left: -2px;
    width: 44px;
    height: 44px;
  }

  .c-cb > input[type="checkbox"] {
    -webkit-font-smoothing: antialiased;
    cursor: pointer;
    position: absolute;
    z-index: 1;
    margin: 0;
    opacity: 0;
  }

  .c-cb > label {
    font-size: inherit;
    font-family: inherit;
    line-height: inherit;
    display: inline-block;
    margin-bottom: 0;
    padding: 8px 15px 5px;
    cursor: pointer;
    touch-action: manipulation;
  }

  .c-cb > label::before {
    content: "";
    position: absolute;
    border: 2px solid currentColor;
    background: transparent;
  }

  .c-cb > input[type="checkbox"]:focus + label::before {
    border-width: 4px;
    outline: 3px dashed #228bec;
  }

  .c-cb > label::after {
    box-sizing: content-box;
    content: "";
    position: absolute;
    top: 11px;
    left: 9px;
    width: 18px;
    height: 7px;
    transform: rotate(-45deg);
    border: solid;
    border-width: 0 0 5px 5px;
    border-top-color: transparent;
    opacity: 0;
    background: transparent;
  }

  .c-cb > input[type="checkbox"]:checked + label::after {
    opacity: 1;
    color: rgb(3, 141, 3);
  }